import React,{useState} from 'react';
const AddComment = ({setArticleInfo,articleName}) => {
    const[username,setUsername]= useState('');
    const[commentsText,setcommentsText] = useState('');
    const handleAddComments= async () =>{
        const result= await fetch(`/api/articles/${articleName}/add-comment`,{
            method:"post",
            body:JSON.stringify({username:username,text:commentsText}),
            headers:{
                'Content-Type': 'application/json'
            }

        })
        const body = await result.json();
        setArticleInfo(body);
    }
    
    return(
    <>
    <h3>Add a Comment</h3>
    <label>
        Name:
    <input type="text" onChange={(event)=>setUsername(event.target.value)} value={username}/>
    </label>
    <label>Comments
    <textarea onChange={(event)=>setcommentsText(event.target.value)} value={commentsText} rows="4" cols="50" />
    </label>
    <button  onClick={()=>handleAddComments()}>Add Comment</button>
 </>
 )
}
export default AddComment;