import React from 'react';
import { Link } from 'react-router-dom';
const ArticleComponent  = ({articlesprop}) => {
    return ( <React.Fragment>
        <h1>Article List</h1>
    {articlesprop.map((a,key)=>(
        <div key={key}>
        <Link key={key} to={`/article/${a.name}`}>
        <h5>{a.title}</h5>
      
        </Link>
        <p>{a.content[0].substring(0,16)}...</p>
        </div>
    ))}
    </React.Fragment>  );
}
 
export default ArticleComponent ;