import React from 'react';
import './navstyle.css'
import { Link } from 'react-router-dom';
const NavBar = () => {
    return (
        <center>
        <nav>
            <ul>
                <li>
                    <Link className="links" to="/">Home</Link>
                    <Link className="links" to="/about">About</Link>
                    <Link className="links" to="/articlelist">ArticleList</Link>
                    {/* <Link className="links" to="/article">Article</Link> */}
              
                </li>
            </ul>
        </nav>
        </center>
      );
}
 
export default NavBar;