import React,{useState,useEffect}  from 'react'
import articles from '../data/article-content';
import ArticleComponent from './ArticleComponent';
import NotFoundPage from './../common/notFoundPage';
import CommentList from './commentslist';
import UpVoteSection from './upvotesection';
import AddComment from './addComentForm';
const ArticlePage = ({match}) => {
    const name = match.params.name;
    const article = articles.find(article=>article.name===name)
    const [articleinfo,setArticleInfo] = useState({upvotes:0,comments:[]});
    useEffect(()=>{
      const fetchdata = async()=>{
        const result = await fetch(`/api/articles/${name}`);
        const  body =  await result.json();
        console.log(body);
        setArticleInfo(body);
      }
      fetchdata();
    },[name])
    if(!article)
    return(<NotFoundPage/>)
    const other= articles.filter(a=>a.name!==name)
    return ( 
        <React.Fragment>
    <h1>{article.title}</h1>
    <UpVoteSection setArticleInfo={setArticleInfo} articleName={name} upvotes={articleinfo.upvotes}/>
   
    {article.content.map((a,key)=>(
        <div key={key}>
        <p key={key}>{a}</p>
        <CommentList  comments={articleinfo.comments}/>
        <AddComment setArticleInfo={setArticleInfo} articleName={name}/>
        <h3>Other articles</h3>
        <ArticleComponent articlesprop={other}/>
        </div>
    ))}
    </React.Fragment>
     );
}
 
export default ArticlePage;